import request from "@/utils/request.js";

// 获取注册验证码接口
export function getCode(data) {
    return request({
        url: '/sms',
        method: 'post',
        data: data
    })
}

// 注册账户接口
export function goRegister(data) {
    return request({
        url: '/register',
        method: 'post',
        data: data
    })
}

// 账户登录接口
export function goLogin(data) {
    return request({
        url: '/login',
        method: 'post',
        data: data
    })
}