import request from "@/utils/request.js";

// 添加购物车接口
export function addCart(data,token) {
    return request({
        url: '/shoppingCart',
        method: 'post',
        data: data,
        headers:{
            "x-token":token
        }
    })
}

// 获取购物车信息接口
export function getCart(query,token) {
    return request({
        url: '/shoppingCart',
        method: 'get',
        params: query,
        headers: { "x-token":token }
    })
}
